<template>
<div class="login">
  <div class="section">
    <div class="container">
      <div class="field">
        <label class="label">Email</label>
        <p class="control">
          <input class="input" type="email" placeholder="Email Address" v-model="email">
        </p>
      </div>
      <div class="field">
        <label class="label">Password</label>
        <p class="control">
          <input class="input" type="password" placeholder="Password" v-model="password">
        </p>
      </div>
      <div class="field">
        <p class="control">
          <input class="button button-primary" type="submit" value="Submit" @click="submitLogin" />
        </p>
      </div>
      <div class="field">
        <p class="control">
          <router-link to="/register">Need an account?</router-link>
        </p>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {
  mapActions
} from 'vuex';

export default {
  name: 'Login',
  data() {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    submitLogin() {
      this.login({
          email: this.email,
          password: this.password
        })
        .then(() => {
          this.$router.push({
            path: '/account'
          });
        })
        .catch(err => console.log(err));
    },
    ...mapActions(['login'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  padding: 2rem;
}

.container {
  margin: 0 auto;
  max-width: 40%;
}

.control {
  font-size: 1rem;
  position: relative;
  text-align: left;
}

.input {
  align-items: center;
  border: 1px solid transparent;
  border-radius: 3px;
  box-shadow: none;
  display: inline-flex;
  font-size: 1rem;
  height: 2.25em;
  justify-content: flex-start;
  line-height: 1.5;
  padding-bottom: calc(0.375em - 1px);
  padding-left: calc(0.625em - 1px);
  padding-right: calc(0.625em - 1px);
  padding-top: calc(0.375em - 1px);
  position: relative;
  vertical-align: top;
  background-color: white;
  border-color: #dbdbdb;
  color: #363636;
  box-shadow: inset 0 1px 2px rgba(10, 10, 10, 0.1);
  max-width: 100%;
  width: 100%;
}

.input:focus,
.input:active {
  outline: none;
}

.input[disabled] {
  cursor: not-allowed;
}

.input:hover {
  border-color: #b5b5b5;
}

.input:focus,
.input:active {
  border-color: #00d1b2;
}

.field:not(:last-child) {
  margin-bottom: 0.75rem;
}

.label {
  color: #363636;
  display: block;
  font-size: 1rem;
  font-weight: 700;
}

.label:not(:last-child) {
  margin-bottom: 0.5em;
}
</style>



