<template>
<div class="contact">
  <div class="container">
    <div class="content">
      <div class="wrapper">
        <blockquote>
          <p>{{currentQuote}}</p>
          <em>Ron Swanson</em>
          <a @click="nextQuote">Get Another</a>
        </blockquote>
        <img src="../assets/fun/swanson.jpeg" />
      </div>
    </div>
  </div>
</div>
</template>

<script>
import swansonQuotes from '../assets/swanson_quotes';
const initIndex = Math.floor(Math.random() * swansonQuotes.length);
export default {
  name: 'Contact',
  data() {
    return {
      quotes: swansonQuotes,
      index: initIndex
    }
  },
  computed: {
    currentQuote() {
      return this.quotes[this.index];
    }
  },
  methods: {
    prevQuote() {
      if (this.index <= 0) {
        this.index = this.quotes.length - 1;
      } else {
        this.index--;
      }
    },
    nextQuote() {
      if (this.index >= this.quotes.length - 1) {
        this.index = 0;
      } else {
        this.index++;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.contact {
  padding: 2rem;
}

.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0 8rem;
}

.wrapper h4 {
  font-family: cursive;
}

.wrapper img {
  width: 384px;
  height: 512px;
  margin-left: 4rem;
}

blockquote {
  min-width: 640px;
  min-height: 180px;
  display: relative;
  margin-right: 4rem;
  background: #fff;
  padding: 15px 20px 15px 45px;
  margin: 0 0 20px;
  position: relative;

  /*Font*/
  font-family: Georgia, serif;
  font-size: 16px;
  line-height: 1.2;
  color: #666;
  text-align: justify;

  /*Borders - (Optional)*/
  border-left: 15px solid #00d1b2;
  border-right: 2px solid #00d1b2;

  /*Box Shadow - (Optional)*/
  box-shadow: 2px 2px 15px #ccc;
}

blockquote p {
  font-size: 1.7rem;
  flex: 1 0 100%;
}

blockquote em,
blockquote a {
  font-size: 1.4rem;
}

blockquote::before {
  content: "\201C";
  /*Unicode for Left Double Quote*/
  /*Font*/
  font-family: Georgia, serif;
  font-size: 60px;
  font-weight: bold;
  color: #999;

  /*Positioning*/
  position: absolute;
  left: 10px;
  top: 5px;
}

blockquote::after {
  /*Reset to make sure*/
  content: "";
}

blockquote a {
  text-decoration: none;
  cursor: pointer;
  color: #00d1b2;
  user-select: none;
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 14px;
}

blockquote a:hover {
  color: #666;
}

blockquote em {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 14px;
  font-style: italic;
}
</style>



